body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 8vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


* {
	margin: 0;
}

.header{
	display: -webkit-flex;
	display: flex;
	justify-self: flex-end;
	-webkit-align-items: center;
	        align-items: center;
	background-color: #282c34;
	padding: 10px 0px;
}

.header__icon{
	margin-left: 20px;
	height: 40px;
	cursor: pointer;
	/*padding-right: 100vh;*/
	margin-right: auto;
}


a{
	font-weight: 500;
	font-size: 16px;
	background-color: #282c34;
	color: #edf0f1;
	text-decoration: none;
	padding: 20px 20px;
	cursor: pointer;
	border-radius: 5px;
	margin-left: 10px;


}
a:hover{
	background-color: #edf0f1;
	color: #282c34;
	border-radius: 5px;
	cursor: pointer;
	transition: 0.3s;

}

.header__left{
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex: 1 1;
	        flex: 1 1;
	padding: 5px;
	padding-left: 5px;
	height: 30px;
	background-color: red;
	text-align: right;
	/*border: 1px solid lightgray;*/
	/*border-radius: 9px;*/
}.header__center{
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex: 1 1;
	        flex: 1 1;
	width: 10px;
	padding: 5px;
	height: 30px;
	background-color: blue;
	text-align: right;

	/*border: 1px solid lightgray;*/
	/*border-radius: 9px;*/
}
.header__right{
	text-align: right;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex: 1 1;
	        flex: 1 1;
	/*max-width: fit-content;*/
	padding: 5px;
	height: 30px;
	background-color: green;
}
.BackgroundImage{			
    position: relative;
}		

h1 {
    text-align:center;
}

